import * as yup from 'yup';

export const validationSchema = yup.object().shape({
  name: yup
    .string()
    .matches(
      /^[a-zA-Z-- ]+$/,
      '  ,   '
    )
    .min(3, '    3 ')
    .required('    '),
  lastName: yup
    .string()
    .matches(
      /^[a-zA-Z-- ]+$/,
      '  ,    '
    )
    .min(3, '    3 ')
    .required('    '),
  age: yup
    .number()
    .typeError('   ')
    .min(13, '    12')
    .required('    '),
  address: yup
    .string()
    .min(3, '    3 ')
    .required('    '),
  phone: yup
    .string()
    .matches(
      /^\(\d{3}\)\d{3}-\d{2}-\d{2}$/,
      '     (###)###-##-##'
    )
    .required('    '),
});
